import React from 'react';
import PropTypes from 'prop-types';
import { bindActionCreators } from 'redux';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';
import { translate } from 'react-i18next';
import update from 'immutability-helper';

import Header from '../../components/header/header';
import Footer from '../../components/footer/footer';
import Data from '../../components/data/data';

import * as bookActions from '../../actions/book';
import data from '../../../public/data/ERC20.json';

import './currency.scss';

const mapStateToProps = (state) =>
{
    return {
        from: state.book.from
    };
};

const mapDispatchToProps = (dispatch) =>
{
    return {
        actions: {
            bookActions: bindActionCreators(bookActions, dispatch)
        }
    };
};

class Currency extends React.Component
{
    constructor(props)
    {
        super(props);
        this.state = {
            tokenData: data.find(d => d.address === props.from.address) || data[0]
        };

        this.renderSidebar = this.renderSidebar.bind(this);
    }

    renderSidebar()
    {
        return data.map((d) =>
        {
            return (
                <li
                    key={d.address}
                    className={d.address === this.state.tokenData.address ? 'active' : ''}
                    onClick={() =>
                    {
                        const token = data.find(t => t.address === d.address);
                        this.setState(update(this.state, {
                            tokenData: { $set: token }
                        }));
                    }}
                >
                    <span>{d.token}</span>
                </li>
            );
        });
    }

    render()
    {
        const { t } = this.props;
        const { tokenData } = this.state;
        return (
            <div className="co_currency">
                <Header />
                <nav>
                    <span><Link to="/exchange">{t('header.exchange')}</Link></span>
                    <img src="/img/icon/ic-breadthumb.svg" alt="arrow" />
                    <span>{t('header.currency')}</span>
                </nav>
                <main>
                    <div className="sidebar">
                        <div className="title">{t('currency.title')}</div>
                        <ul>
                            {this.renderSidebar()}
                        </ul>
                    </div>
                    <div className="content">
                        <Data tokenData={tokenData} />
                    </div>
                </main>
                <Footer />
            </div>
        );
    }
}

Currency.defaultProps = {
    t: () => {}
};

Currency.propTypes = {
    t: PropTypes.func,
    from: PropTypes.object.isRequired
};

export default connect(mapStateToProps, mapDispatchToProps)(translate()(Currency));
