import React from 'react';
import PropTypes from 'prop-types';
import { translate } from 'react-i18next';
import update from 'immutability-helper';
import { scnToStr } from '../../utils/format';
import './data.scss';

class Data extends React.Component
{
    constructor(props)
    {
        super(props);
        this.state = {};
        this.lange = props.i18n.language;
    }


    shouldComponentUpdate(nextProp)
    {
        if ((nextProp.tokenData.address !== this.props.tokenData.address) || nextProp.i18n.language !== this.lang)
        {
            this.lang = nextProp.i18n.language;
            return true;
        }
        else
        {
            this.lang = null;
            return false;
        }
    }

    render()
    {
        const { t, tokenData } = this.props;
        if (tokenData.address && tokenData && tokenData.fullname)
        {
            return (
                <div className="c_data">
                    <div className="title">
                        {tokenData.token}
                    </div>
                    <div className="info">
                        <div>
                            <div className="name">
                                <h2>{tokenData.fullname}</h2>
                            </div>
                            <div className="intro">
                                <h3>{t('data.intro')}</h3>
                                <p>
                                    {tokenData.introduction}
                                </p>
                            </div>
                        </div>
                        <div className="detail">
                            <div>
                                <div>
                                    {t('data.publish')}
                                </div>
                                <div>{scnToStr(tokenData.totalSupply)} {tokenData.token}</div>
                            </div>
                            <div>
                                <div>
                                    {t('data.flow')}
                                </div>
                                <div>{scnToStr(tokenData.totalSupply)} {tokenData.token}</div>
                            </div>
                            {/* <div className="price">
                                <div>{t('data.price')}</div>
                                <div>
                                    眾籌價格
                                </div>
                            </div> */}
                            <div className="whitebook">
                                <div>{t('data.whitebook')}</div>
                                <div>
                                    <a href={tokenData.whitepaper} target="_blank" rel="noopener noreferrer">{tokenData.whitepaper}</a>
                                </div>
                            </div>
                            <div className="web">
                                <div>
                                    {t('data.web')}
                                </div>
                                <div>
                                    <a href={tokenData.officialSite} target="_blank" rel="noopener noreferrer">{tokenData.officialSite}</a>
                                </div>
                            </div>
                            <div className="block">
                                <div>
                                    {t('data.block')}
                                </div>
                                <div>
                                    <a href={tokenData.blockinfo} target="_blank" rel="noopener noreferrer">{tokenData.blockinfo}</a>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            );
        }
        else
        {
            return null;
        }
    }
}

Data.defaultProps = {
    t: () => {},
    tokenData: {},
    i18n: {}
};

Data.propTypes = {
    t: PropTypes.func,
    tokenData: PropTypes.object,
    i18n: PropTypes.object
};

export default translate('common', { wait: true })(Data);
